import Vue from 'vue';
import Vuex from 'vuex';
import {
  setData,
  resultField,
  newLotteryField,
  listField
} from '@/helper/index';
import defaultState, {
  defaultResult,
  defaultNewLottery,
  defaultPhotos,
  defaultList,
  defaultConfig
} from './defaultState';

Vue.use(Vuex);

export default new Vuex.Store({
  state: defaultState,
  mutations: {
    setClearConfig(state) {
      console.log(defaultConfig, defaultNewLottery);
      state.config = defaultConfig;
      state.newLottery = defaultNewLottery;
    },
    setClearList(state) {
      state.list = defaultList;
    },
    setClearPhotos(state) {
      state.photos = defaultPhotos;
    },
    setClearResult(state) {
      state.result = defaultResult;
    },
    setClearStore(state) {
      state.config = defaultConfig;
      state.result = defaultResult;
      state.newLottery = defaultNewLottery;
      state.list = defaultList;
      state.photos = defaultPhotos;
    },
    setConfig(state, config) {
      state.config = config;
    },
    setResult(state, result = {}) {
      state.result = result;

      setData(resultField, state.result);
    },
    setNewLottery(state, newLottery) {
      if (state.newLottery.find(item => item.name === newLottery.name)) {
        return;
      }
      state.newLottery.push(newLottery);
      setData(newLotteryField, state.newLottery);
    },
    setList(state, list) {
      const arr = state.list;
      list.forEach(item => {
        const arrIndex = arr.findIndex(data => data.key === item.key);
        if (arrIndex > -1) {
          arr[arrIndex].name = item.name;
        } else {
          arr.push(item);
        }
      });
      state.list = arr;

      setData(listField, arr);
    },
    setPhotos(state, photos) {
      state.photos = photos;
    }
  },
  actions: {},
  modules: {}
});
